import React from 'react';
import PropTypes from 'prop-types';
import Icon from 'UI/Icon';
import SearchBox from 'UI/SearchBox';
import Blurb from 'Components/Blurb';
import MockBlurb from 'Components/MockBlurb';
import PWAButton from 'Components/PWAButton'
import './SplashPage.css';

const SplashPage = (props) => {
  const { section } = props;
  const { VITE_ctc } = import.meta.env;
  let ctc = true;
  if (VITE_ctc === 'false') {
    ctc = false;
  }

  const handleOpenImport = () => {
    const event = new CustomEvent('openImportModal');
    window.dispatchEvent(event);
  };

  const getIcon = () => {
    let iconName = null;
    switch (section.toLowerCase()) {
      case 'fs':
        iconName = 'force-structure';
        break;
      case 'tc':
        iconName = 'training-circulars';
        break;
      default:
        iconName = section.toLowerCase();
        break;
    }
    if (iconName) {
      return (
        <div className="splash-icon">
          <Icon file={iconName} />
        </div>
      );
    }
    return null;
  };

  const getTitle = () => {
    switch (section.toLowerCase()) {
      case 'date':
        return 'DATE Knowledge Base';
      case 'fs':
        return 'Force Structure';
      case 'tc':
        return 'Training Publications';
      case 'weg':
        if (ctc) return 'CTC Equipment Guide';
        return 'Worldwide Equipment Guide';
      case 'voa':
        return 'Virtual OPFOR Academy';
      default:
        return null;
    }
  };

  const handleOpenInstructions = () => {
    console.log('handleOpenInstructions called');
    const event = new CustomEvent('openInstallModal');
    window.dispatchEvent(event);
  };

  const installButton = (
    <button
      onClick={handleOpenInstructions}
      style={{
        border: 'none',
        backgroundColor: '#e6ba08',
        padding: '10px',
        borderRadius: '8px',
        cursor: 'pointer',
        display: 'inline-flex',
        alignItems: 'center',
        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
        backdropFilter: 'blur(5px)',
        transition: 'transform 0.1s ease-in-out',
      }}
      onMouseDown={(e) => (e.currentTarget.style.transform = 'scale(0.95)')}
      onMouseUp={(e) => (e.currentTarget.style.transform = 'scale(1)')}
      onMouseLeave={(e) => (e.currentTarget.style.transform = 'scale(1)')}
    >
      <img
        src="download_button.png"
        alt="download button"
        style={{ width: '24px', height: '24px', marginRight: '8px' }}
      />
      {/* <img
        src="Progressive_Web_Apps_Logo.svg"
        alt="Install App"
        style={{ width: '32px', height: '32px', marginRight: '8px' }}
      /> */}
      <span style={{ fontWeight: '600', fontSize: '14px', color: '#000' }}>
        Install WEG Application
      </span>
    </button>
  );

  return (
    <div className={`splash-page ${section.toLowerCase()}`}>
      <div className="splash-title">
        {getIcon()}
        {getTitle()}
      </div>
      <SearchBox section={section} />
      <Blurb section={section} additionalClass="splash-text" />
      <div style={{
            padding: '10px',
          }}/>
      <MockBlurb section="PWA Announcement" additionalClass="splash-text" />
      <div style={{
            padding: '10px',
          }}/>
      <div style={{display: 'flex', justifyContent: 'space-between', columnGap: '20px'}}>
        {installButton}
        <button
          onClick={handleOpenImport}
          style={{
            border: 'none',
            backgroundColor: '#e6ba08',
            padding: '10px',
            borderRadius: '8px',
            cursor: 'pointer',
            display: 'inline-flex',
            alignItems: 'center',
            boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
            backdropFilter: 'blur(5px)',
            transition: 'transform 0.1s ease-in-out',
            height: '52px',
          }}
          onMouseDown={(e) => (e.currentTarget.style.transform = 'scale(0.95)')}
          onMouseUp={(e) => (e.currentTarget.style.transform = 'scale(1)')}
          onMouseLeave={(e) => (e.currentTarget.style.transform = 'scale(1)')}
        >
          <img
            src="favicon.png"
            alt="TRADOC Logo"
            style={{ width: '24px', height: '24px', marginRight: '8px' }}
          />
          <span style={{ fontWeight: '600', fontSize: '14px', color: '#000' }}>
            WEG Import Manager
          </span>
        </button>
      </div>
      <div style={{
            padding: '10px',
          }}/>
    </div>
  );
};

SplashPage.propTypes = {
  /**
   * The section splash page to display
   */
  section: PropTypes.string,
};

SplashPage.defaultProps = {
  section: null,
};

export default SplashPage;
