import React, { useRef, useEffect, useState } from 'react';
import './TextLog.css';

const TextLog = React.memo(({ messages, importantMessages }) => {
  const consoleRef = useRef(null);
  const [isAtBottom, setIsAtBottom] = useState(true);
  const [importantMessagesOnly, setImportantMessagesOnly] = useState(false);
  const [activeLog, setActiveLog] = useState(messages);


  useEffect(() => {
    const consoleNode = consoleRef.current;
    if (!consoleNode) return;

    const handleScroll = () => {
      const { scrollTop, scrollHeight, clientHeight } = consoleNode;
      const atBottom = scrollHeight - scrollTop <= clientHeight + 1; // Adding a small buffer
      setIsAtBottom(atBottom);
    };

    consoleNode.addEventListener('scroll', handleScroll);

    // Clean up the event listener on component unmount
    return () => {
      consoleNode.removeEventListener('scroll', handleScroll);
    };
  }, []); // Run once on mount

  useEffect(() => {
    if (isAtBottom && consoleRef.current) {
      consoleRef.current.scrollTop = consoleRef.current.scrollHeight;
    }
  }, [messages, importantMessages, isAtBottom]);

  useEffect(() => {
    setActiveLog(importantMessagesOnly ? importantMessages : messages);
  }, [messages, importantMessages, importantMessagesOnly]);

  const getMessageStyle = (type) => {
    switch (type) {
      case 'warn':
        return { color: 'yellow' };
      case 'error':
        return { color: 'red' };
      case 'success':
        return { color: 'green' };
      default:
        return { color: '#d4d4d4' };
    }
  };

  const scrollToBottom = () => {
    if (consoleRef.current) {
      consoleRef.current.scrollTop = consoleRef.current.scrollHeight;
      setIsAtBottom(true);
    }
  };

  const handleToggleImportantMessages = () => {
    setImportantMessagesOnly(!importantMessagesOnly)
  }


  return (
    <div className='text-log-container'>
      <div className="text-log" ref={consoleRef}>
        {activeLog.map((msg, idx) => (
          <div key={idx} style={getMessageStyle(msg.type)}>
            {msg.text}
          </div>
        ))}
      </div>
      <div className="log-buttons">
        <div className="level-button">
          <div className="level-label">Filter level:</div>
          <button onClick={handleToggleImportantMessages}>
            {importantMessagesOnly ? 'Warn' : 'Info'}
          </button>
        </div>
        <div className="scroll-button">
          {!isAtBottom && (
            <button onClick={scrollToBottom}>Scroll to End</button>
          )}
        </div>
      </div>
    </div>
  );
});

export default TextLog;
