import React, {
  Fragment,
  useState,
  useEffect,
  useRef,
  useCallback,
} from 'react';
import Modal from 'UI/Modal';
import TextLog from 'Components/TextLog';
import { beginImageImport } from 'dataAccess';
import { getMiscByKey, initLocalDB, resetLocalDB, getOnlineStatus, toggleOnline, addMisc } from 'dataAccess';
import throttle from 'lodash.throttle';
import './ImportModal.css';

// Ideas
// - Show if images/assets have already been downloaded?
// Bug: While using app -> go offline
//   - App doesn't realize it's offline and tries to behave as if it's online, causing network requests to fail
// - "Import With Filters" option
//   - Maybe a button to press, which closes the modal and a "save" button appears,
//     which when clicked, reopens the modal and immediately begins the WEG Asset Import
//   - Should display a warning if assets are already imported that they should be deleted first
//     - Maybe it could automatically just deletge

export default function ImportModal({
  close,
  isFullScreen,
  addMessage,
  messages,
  flushMessages,
  importantMessages,
  addImportantMessage,
  assetsCompleted,
  setAssetsCompleted,
  totalAssets,
  setTotalAssets,
  importing,
  setImporting,
  startTime,
  setStartTime,
  estimatedTimeRemaining,
  setEstimatedTimeRemaining,
}) {
  const [isHolding, setIsHolding] = useState(false);
  const [holdProgress, setHoldProgress] = useState(0);
  const holdIntervalRef = useRef(null);
  const [persisted, setPersisted] = useState(false);
  const [isOnline, setIsOnline] = useState(false);
  const [isDefaultImagesChecked, setIsDefaultImagesChecked] = useState(true);
  const [assetImagesLimit, setAssetImagesLimit] = useState(1);
  const [storageEstimate, setStorageEstimate] = useState(0);
  const [storageUsed, setStorageUsed] = useState(0);
  const [importImagesDisabled, setImportImagesDisabled] = useState(true);
  const [controller, setController] = useState(new AbortController());


  const incrementAssetsCompleted = () => {
    console.info("incrementAssetsCompleted!")
    setAssetsCompleted((prevCount) => prevCount + 1);
  };

  const formatTime = (ms) => {
    const totalSeconds = Math.floor(ms / 1000);
    const minutes = Math.floor(totalSeconds / 60);
    const seconds = totalSeconds % 60;
    return `${minutes > 0 ? `${minutes}m ` : ''}${seconds}s`;
  };

  const estimateTime = useCallback(() => {
    if (importing && assetsCompleted >= 3 && assetsCompleted <= totalAssets) {
      const now = Date.now();
      const elapsedTime = now - startTime;
      const averageTimePerAsset = elapsedTime / assetsCompleted;
      const remainingAssets = totalAssets - assetsCompleted;
      const estimateMs = averageTimePerAsset * remainingAssets;
      setEstimatedTimeRemaining(estimateMs);
    } else {
      setEstimatedTimeRemaining(null);
    }
  }, [importing, assetsCompleted, totalAssets, startTime]);

  const estimateTimeRef = useRef(estimateTime);

  useEffect(() => {
    navigator.storage.persisted().then(res => setPersisted(res))
    getMiscByKey('lastAssetImport').then((res) =>
      setImportImagesDisabled(!res)
    );
    setStorageDetails();
    console.info("ImportModal useEffect[]")
    getOnlineStatus().then((online) => setIsOnline(online))
  }, [])

  const setStorageDetails = async () => {
    navigator.storage.estimate().then(estimate => {
      setStorageEstimate(estimate.quota)
      setStorageUsed(estimate.usage)
    });
  }

  useEffect(() => {
    let intervalId;

    getMiscByKey('lastAssetImport').then((res) =>
      setImportImagesDisabled(!res)
    );

    if (importing) {
      intervalId = setInterval(() => {
        setStorageDetails();
        // navigator.storage.estimate().then(estimate => {
        //   setStorageEstimate(estimate.quota)
        //   setStorageUsed(estimate.usage)
        // });
        if (!importing) {
          clearInterval(intervalId);
        }
      }, 1000);
    }

    return () => clearInterval(intervalId);

  }, [importing])

  useEffect(() => {
    estimateTimeRef.current = estimateTime;
  }, [estimateTime]);

  const throttledEstimateTimeRef = useRef(
    throttle(() => {
      estimateTimeRef.current();
    }, 1000)
  ).current;

  useEffect(() => {
    if (importing) {
      throttledEstimateTimeRef();
    }
  }, [
    assetsCompleted,
    totalAssets,
    importing,
    startTime,
    throttledEstimateTimeRef,
  ]);

  useEffect(() => {
    return () => {
      throttledEstimateTimeRef.cancel();
    };
  }, [throttledEstimateTimeRef]);

  const startAssetImport = async () => {
    const abortSignal = controller.signal;
    setImporting(true);
    setAssetsCompleted(0);
    setStartTime(Date.now());
    addMessage('Beginning asset import...', 'info', true);
    try {
      await initLocalDB(
        addMessage,
        addImportantMessage,
        setTotalAssets,
        incrementAssetsCompleted,
        abortSignal
      );
    } catch (error) {
      addMessage(`Failed to import assets: ${error.message}`, 'error');
    } finally {
      if (flushMessages && typeof flushMessages.flush === 'function') {
        flushMessages.flush();
      }
      setImporting(false);
      setEstimatedTimeRemaining(null);
      setController(new AbortController());
    }
  };

  const startImageImport = async () => {
    const abortSignal = controller.signal;
    setImporting(true);
    setAssetsCompleted(0);
    const startTime = Date.now()
    setStartTime(startTime);
    addMessage('Beginning image import...', 'info', true);
    try {
      await beginImageImport(
        addMessage,
        addImportantMessage,
        setTotalAssets,
        incrementAssetsCompleted,
        assetImagesLimit,
        abortSignal
      );
      addMessage('Images imported successfully.', 'success');
      addMisc('lastImageImport',  startTime);
    } catch (error) {
      addImportantMessage(`Failed to import images: ${error.message}`, 'error');
    } finally {
      if (flushMessages && typeof flushMessages.flush === 'function') {
        flushMessages.flush();
      }
      if (!abortSignal.aborted) {
        addImportantMessage('Finished importing images.', 'success');
      }
      setImporting(false);
      setEstimatedTimeRemaining(null);
      setController(new AbortController());
    }
  };

  const handleHoldStart = () => {
    if (importing) return;

    setIsHolding(true);
    setHoldProgress(0);
    const holdDuration = 3000;
    const intervalDuration = 100;
    const totalIntervals = holdDuration / intervalDuration;
    let currentInterval = 0;

    holdIntervalRef.current = setInterval(() => {
      currentInterval += 1;
      setHoldProgress((currentInterval / totalIntervals) * 100);
      if (currentInterval >= totalIntervals) {
        clearInterval(holdIntervalRef.current);
        setHoldProgress(0);
        setIsHolding(false);
        resetLocalDB(addMessage).then(() => {
          getMiscByKey('lastAssetImport').then((res) => {
            console.info('getMiscByKey lastAssetImport after resetLocalDB res:');
            console.dir(res);
            setImportImagesDisabled(!res);
          });
          setStorageDetails();
        });
      }
    }, intervalDuration);
  };

  const handleHoldEnd = () => {
    setIsHolding(false);
    setHoldProgress(0);
    if (holdIntervalRef.current) {
      clearInterval(holdIntervalRef.current);
    }
  };

  const handleDefaultImagesChanged = () => {
    setIsDefaultImagesChecked(!isDefaultImagesChecked);
  };

  const requestPersistentStorage = async () => {
    if (navigator.storage && navigator.storage.persist) {
      const isPersisted = await navigator.storage.persisted();
      if (!isPersisted) {
        const granted = await navigator.storage.persist();
        console.log(`Storage persistence granted: ${granted}`);
        if (granted) {
          addImportantMessage('Persistent storage has been granted!', "success");
          setPersisted(true);
        } else {
          addImportantMessage('Persistent storage request was denied. Try again in Chrome after installing as an app.', "error");
        }
      } else {
        console.log("Persistent storage already granted!");
        addImportantMessage('Persistent storage is already granted.', "warn");
      }
    } else {
      console.log("Persistent storage API not supported.");
      addImportantMessage('Persistent storage is not supported in your browser.', "error");
    }
  };

  const handleCancel = () => {
    addImportantMessage('Cancelling import in progress.', 'error');
    controller.abort();
  }

  /**
 * Converts bytes into a human-readable format.
 * @param {number} bytes - The number of bytes.
 * @param {number} decimals - Number of decimal places.
 * @returns {string} - Formatted string with appropriate unit.
 */
function formatBytes(bytes, decimals = 2) {
  if (bytes === 0) return '0 Bytes';

  const k = 1024;
  const dm = decimals < 0 ? 0 : decimals;

  const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB'];

  const i = Math.floor(Math.log(bytes) / Math.log(k));

  return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
}


  return (
    <Fragment>
      <Modal
        showShare={false}
        open={() => ({
          section: 'WEG',
          title: 'Import WEG',
          children: (
            <Fragment>
              <div className="button-group">
                {/* <button
                  onClick={() => {
                    toggleOnlineStatus();
                    addMessage('Toggled online status.');
                  }}
                >
                  {isOnline ? 'Online' : 'Offline'}
                </button> */}
                <button
                  disabled={persisted}
                  className="import-button"
                  onClick={requestPersistentStorage}
                >
                  {persisted ? 'Storage Enabled' : 'Enable Storage'}
                </button>
                <button
                  className="import-button"
                  disabled={importing}
                  onClick={startAssetImport}
                >
                  {importing ? 'Importing...' : 'Import Assets'}
                </button>
                <div className="import-fields">
                  <button
                    className="import-button"
                    disabled={importing || importImagesDisabled}
                    onClick={startImageImport}
                  >
                    {importing ? 'Importing...' : 'Import Images'}
                  </button>
                  {/* <div className="defaultImagesCheckbox">
                  <input
                    type="checkbox"
                    disabled={importing}
                    checked={isDefaultImagesChecked}
                    onChange={handleDefaultImagesChanged}
                  />
                  <label>
                    {isDefaultImagesChecked ? 'Limit ' : 'All Images'}
                  </label>
                </div> */}
                  <div className="asset-images-limit">
                    <input
                      disabled={importing}
                      type="number"
                      value={assetImagesLimit}
                      onChange={(event) =>
                        setAssetImagesLimit(
                          Math.min(Math.max(1, event.target.value), 9)
                        )
                      }
                    />
                    <label>Limit</label>
                  </div>
                </div>
                {importing ? (
                  <button className="delete-button" onClick={handleCancel}>
                    Cancel
                  </button>
                ) : (
                  <button
                    className={`delete-button ${isHolding ? 'holding' : ''}`}
                    onMouseDown={handleHoldStart}
                    onMouseUp={handleHoldEnd}
                    onMouseLeave={handleHoldEnd}
                    onTouchStart={handleHoldStart}
                    onTouchEnd={handleHoldEnd}
                    disabled={importing}
                  >
                    {importing
                      ? 'Importing...'
                      : isHolding
                        ? 'Hold to Delete'
                        : 'Clear Storage'}
                    {isHolding && (
                      <div className="hold-progress">
                        <div
                          className="hold-progress-bar"
                          style={{ width: `${holdProgress}%` }}
                        ></div>
                      </div>
                    )}
                  </button>
                )}
              </div>

              <div className="text-log-box">
                <TextLog
                  className="error-log"
                  messages={messages}
                  importantMessages={importantMessages}
                />
              </div>

              <div className="modal-info-container">
                {/* <div className="error-log-title">
                <b>Error Log:</b>
              </div> */}

                {storageEstimate ? (
                  <div className="storage-info">
                    <div>
                      Storage used: {formatBytes(storageUsed)}
                      {` (${(100 * (storageUsed / storageEstimate)).toFixed(3)}%)`}
                    </div>
                    <div>
                      Storage available: {formatBytes(storageEstimate)}
                      {}
                    </div>
                  </div>
                ) : null}
              </div>
              <div className="progress-container">
                {importing && (
                  <>
                    <progress
                      className="import-progress"
                      value={assetsCompleted}
                      max={totalAssets}
                    />
                    <div className="progress-info">{`${assetsCompleted} / ${totalAssets} assets imported.`}</div>
                    {assetsCompleted >= 3 ? (
                      estimatedTimeRemaining !== null ? (
                        <div className="estimated-time">
                          Estimated time remaining:{' '}
                          {formatTime(estimatedTimeRemaining)}
                        </div>
                      ) : (
                        <div className="estimated-time">
                          Calculating estimated time...
                        </div>
                      )
                    ) : (
                      <div className="estimated-time">
                        Estimating time remaining...
                      </div>
                    )}
                  </>
                )}
              </div>
            </Fragment>
          ),
          modalStyle: isFullScreen
            ? { width: '100%', height: '100%' }
            : { width: '83%', height: '83%' },
        })}
        autoOpen
        close={close}
        identifier="Import Identifier"
      />
    </Fragment>
  );
}
